	module.exports = {"data":{"markdownRemark":{"html":"<h4>Suggested Reading:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Unsupervised_learning\">https://en.wikipedia.org/wiki/Unsupervised_learning</a></li>\n<li><a href=\"https://stackoverflow.com/a/1854449/6873133\">https://stackoverflow.com/a/1854449/6873133</a></li>\n<li><a href=\"http://mlg.eng.cam.ac.uk/zoubin/papers/ul.pdf\">http://mlg.eng.cam.ac.uk/zoubin/papers/ul.pdf</a></li>\n</ul>\n<h4>Draft of Article:</h4>\n<!-- Please add your working draft below in GitHub-flavored Markdown -->\n<!--\nDiscussion points:\n- Unsupervised learning doesn't have a correct answer i.e. you can't be more or less accurate in the output\n- Learn \"hidden\" structure in data\n- Clustering is classical example\n- Group like things together\n- Example use case: movie database with people's preferences, you want to cluster and see different types of people\n- Example use case: grouping documents or articles of similar content\n-->\n<p>What is Unsupervised Learning?</p>\n<p>Unsupervised learning allows us to approach problems with little or no idea what our results should look like. We can derive structure from data where we don’t necessarily know the effect of the variables.</p>\n<p>Example:</p>\n<p>Clustering: Is used for exploratory data analysis to find hidden patterns or grouping in data. Take a collection of 1,000,000 different genes, and find a way to automatically group these genes into groups that are somehow similar or related by different variables, such as lifespan, location, roles, and so on.</p>\n<p>Few more Examples:\nSuppose you have data for an E-commerce site. You have a list of people and things they have ordered online this last week. You can now use Clustering Algorithms and find the pattern in the data, predict the buying trend and formulate the business strategy as per the trend.</p>","fields":{"slug":"/machine-learning/unsupervised-learning/"},"frontmatter":{"title":"Unsupervised Learning"}}},"pathContext":{"slug":"/machine-learning/unsupervised-learning/"}}

